  width: 400px;
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  color: #ffffff;
  position: relative;
  border: 2px solid #333;
}

/* ‰πêÁõòÂä®Áîª */
.vinyl-container {
  width: 200px;
  height: 200px;
  margin: 0 auto 30px;
  perspective: 1000px;
}

.vinyl-record {
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, #000000 0%, #333333 70%, #000000 71%, #000000 100%);
  border-radius: 50%;
  position: relative;
  transform-style: preserve-3d;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
}

.vinyl-center {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #ffffff 0%, #cccccc 100%);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.vinyl-grooves {
  position: absolute;
  top: 15%;
  left: 15%;
  right: 15%;
  bottom: 15%;
  border-radius: 50%;
  background: repeating-radial-gradient(
    circle at center,
    #333 0%,
    #000 2%,
    #333 4%,
    #000 6%,
    #333 8%
  );
  opacity: 0.6;
}

/* ‰πêÁõòÊóãËΩ¨Âä®Áîª */
@keyframes vinylSpin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* ÊÇ¨ÊµÆÂõæÁâáÊ†∑Âºè */
.floating-image {
  position: fixed;
  z-index: 10;
  opacity: 0.7;
  transform: scale(0.9);
  transition: all 0.5s ease;
  animation: floatAnimation 8s ease-in-out infinite;
  pointer-events: none;
  width: 220px;
  height: 280px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* Êí≠ÊîæÂô®‰∏äÂçäÈÉ®ÂàÜÂõæÁâáÊ†∑Âºè */
.player-top-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
  position: relative;
  margin-bottom: 20px;
}

.top-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.music-player-container:hover .top-image {
  transform: scale(1.05);
