      </div>
    </section>

    <!-- Èü≥‰πêÊí≠ÊîæÂô®Âå∫Âüü -->
    <section class="music-section">
      <div class="container">
        <h2 class="section-title" data-aos="fade-up">üéµ ‰∏çÂê¨miletÁöÑ‰∫∫ÁîüÊòØÁõ∏ÂØπÂ§±Ë¥•ÁöÑ</h2>
        <div class="music-player-wrapper">
          <MusicPlayer />
        </div>
      </div>
    </section>

    <!-- Â∫ïÈÉ®Âå∫Âüü -->
    <footer class="footer-section">
      <div class="container">
        <div class="footer-content">
          <div class="haruhi-image">
            <img 
              src="" 
              data-src="../assets/images/ÂáâÂÆ´Êò•Êó•.jpg" 
              alt="ÂáâÂÆ´Êò•Êó•" 
              class="lazy-load-image"
            />
          </div>
          <div class="haruhi-quote">
            ÊàëÂØπÊôÆÈÄöÁöÑ‰∫∫Á±ªÊ≤°ÊúâÂÖ¥Ë∂£Ôºå‰Ω†‰ª¨ÂΩì‰∏≠Ë¶ÅÊòØÊúâÂ§ñÊòü‰∫∫„ÄÅÊú™Êù•‰∫∫„ÄÅÂºÇ‰∏ñÁïå‰∫∫‰ª•ÂèäË∂ÖËÉΩÂäõËÄÖÁöÑËØùÔºåÂ∞±Â∞ΩÁÆ°Êù•ÊâæÊàëÂêßÔºÅ
          </div>
          <a href="https://github.com/shiki-desu" target="_blank" class="github-icon">
            <img 
              src="" 
              data-src="../assets/images/github.png" 
