<script setup lang="ts">
// ÂØºÂÖ•ÁªÑ‰ª∂
import BannerSection from '../components/BannerSection.vue';
import ArticleCard from '../components/ArticleCard.vue';
import DetailModal from '../components/DetailModal.vue';
import BackToTop from '../components/BackToTop.vue';
import MusicPlayer from '../components/MusicPlayer.vue';
import AnimeRecommendation from '../components/AnimeRecommendation.vue';
import DataDisplay from '../components/DataDisplay.vue';

// ÂØºÂÖ•Â§ñÈÉ®ËµÑÊ∫ê
import { ref, onMounted } from 'vue';
import { Article, getArticles, getArticleById } from '../services/articleService';
import { disablePageScroll, enablePageScroll, addFadeInAnimation } from '../utils/scrollUtils';

// Âä†ËΩΩÊ†∑Âºè
import '../assets/styles/shikiHomeStyles.css';

// Êï∞ÊçÆÂíåÁä∂ÊÄÅ
const articles = ref<Article[]>(getArticles());
const showDetail = ref(false);
const currentDetail = ref<Article | null>(null);
const isLoaded = ref(false);

// ÊâìÂºÄËØ¶ÊÉÖÂºπÁ™ó
const openDetail = (id: number) => {
  const detail = getArticleById(id);
  if (detail) {
    currentDetail.value = detail;
    showDetail.value = true;
    disablePageScroll();
  }
};

// ÂÖ≥Èó≠ËØ¶ÊÉÖÂºπÁ™ó
const closeDetail = () => {
  showDetail.value = false;
  enablePageScroll();
};

// È°µÈù¢Âä†ËΩΩÂÆåÊàê
onMounted(() => {
  // Ê®°ÊãüÈ°µÈù¢Âä†ËΩΩ
  setTimeout(() => {
    isLoaded.value = true;
    
    // Ê∑ªÂä†Âç°ÁâáÁöÑÊ∏êÂÖ•Âä®Áîª
    const cards = Array.from(document.querySelectorAll('.article-card-item')) as HTMLElement[];
    addFadeInAnimation(cards);
    
    // Âª∂ËøüÂä†ËΩΩÂ∫ïÈÉ®Âå∫ÂüüÁöÑÂõæÁâáÔºåÊèêÂçáÂàùÂßãÂä†ËΩΩÈÄüÂ∫¶
    setTimeout(() => {
      const lazyImages = document.querySelectorAll('.lazy-load-image');
      lazyImages.forEach(img => {
        const dataSrc = (img as HTMLImageElement).getAttribute('data-src');
        if (dataSrc) {
          (img as HTMLImageElement).src = dataSrc;
          // ÂõæÁâáÂä†ËΩΩÂÆåÊàêÂêéÊ∑ªÂä†Ê∑°ÂÖ•ÊïàÊûú
          (img as HTMLImageElement).onload = function() {
            (this as HTMLElement).style.opacity = '1';
          };
        }
      });
    }, 1500);
  }, 800);
});
</script>